import React, { Component } from 'react';
import './body.scss';
import StepsPost from './steps';
import Step1 from './step-1';
import Step2 from './step-2';
import Button from '../buttons/button';

class BodyPost extends Component{
  constructor(props){
    super(props);
    this.state= {
      step1: false,
      perfilPic: null,
      history: '',
      birthday: '',
      interest: '',
      step2: true,
      frontPic: null,
    }
    this.handleChange = this.handleChange.bind(this);
    this.handleFile = this.handleFile.bind(this);
    this.onNext = this.onNext.bind(this);
  }

  handleChange(e) {
    this.setState({ [e.target.id]: e.target.value });
  }

  handleFile(e) {
    console.log(e.target.id);
    const img = e.target.files[0];
    const reader = new FileReader();
    reader.onloadend = () => {
      this.setState({ [e.target.id]: reader.result });
    }
    if(img) {
      reader.readAsDataURL(img);
    } else {
      this.setState({ perfilPic: null });
    }
  }

  onNext(i) {
    if(i === 2) {
      this.setState({ step1Out: true });
      setTimeout(()=>{
        this.setState({ step1: false, step2: true });
      },200)
    }
  }

  render() {
    const {
      step1,
      step1Out,
      perfilPic,
      history,
      birthday,
      interest,
      step2,
      frontPic,
    } = this.state;
    const valuesStep1 = {
      perfilPic,
      history,
      birthday,
      interest,
    }
    const valuesStep2 = {
      frontPic,
    };
    return(
      <section className="postregistro_body_cont">
        <div className="container-fluid">
          <StepsPost />
          {
            step1 && (
              <Step1
                out={step1Out}
                onChange={this.handleChange}
                onNext={this.onNext}
                onFile={this.handleFile}
                values= {valuesStep1}
                onNext={this.onNext}
            />
          )
          }
          {
            step2 && (
              <Step2
                onFile={this.handleFile}
                values={valuesStep2}
              />
            )
          }
        </div>
        <div className="footer">
          <Button onClick={()=> this.onNext(2)} color="dark" size="md">Siguiente</Button>
        </div>
      </section>
    );
  }
}

export default BodyPost;
